import "./charts.scss";
import {
  AreaChart,
  Area,
  XAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

const data = [
  { Closed: 900, name: "Jan", Resolved: 1200 },
  { Closed: 700, name: "Feb", Resolved: 2100 },
  { Closed: 600, name: "Mar", Resolved: 800 },
  { Closed: 500, name: "Apr", Resolved: 1600 },
  { Closed: 1400, name: "May", Resolved: 900 },
  { Closed: 800, name: "June", Resolved: 1700 },
  { Closed: 900, name: "July", Resolved: 700 },
  { Closed: 1300, name: "Aug", Resolved: 1500 },
  { Closed: 900, name: "Sept", Resolved: 1200 },
  { Closed: 1900, name: "Oct", Resolved: 600 },
  { Closed: 900, name: "Nov", Resolved: 1200 },
  { Closed: 400, name: "Dec", Resolved: 400 },
];

const Chart = ({ aspect, title }) => {
  return (
    <div className="chart">
      <div className="title">{title}</div>
      <ResponsiveContainer width="100%" aspect={aspect}>
        <AreaChart width={730} height={250} data={data}
          margin={{ top: 10, right: 30, left: 30, bottom: 0 }}>
          <defs>
            <linearGradient id="Resolved" x1="0" y1="0" x2="0" y2="1">
              <stop offset="5%" stopColor="#8884d8" stopOpacity={0.8} />
              <stop offset="95%" stopColor="#8884d8" stopOpacity={0} />
            </linearGradient>
            <linearGradient id="colorPv" x1="0" y1="0" x2="0" y2="1">
              <stop offset="5%" stopColor="#82ca9d" stopOpacity={0.8} />
              <stop offset="95%" stopColor="#82ca9d" stopOpacity={0} />
            </linearGradient>
          </defs>
          <XAxis dataKey="name" />
          
          <CartesianGrid strokeDasharray="3 3" vertical={false} />
          <Tooltip />
          <Area 
            type="monotone"
            dataKey="Resolved"
            stroke="#8884d8"
            fillOpacity={1}
            fill="url(#Resolved)"
          />
          <Area 
            type="monotone"
            dataKey="Closed"
            stroke="#8884d8"
            fillOpacity={1}
            fill="url(#colorPv)"
          
          />
        </AreaChart>
      </ResponsiveContainer>
    </div>
  );
};

export default Chart;
