import Home from "./pages/home/Home";
import Login from "./pages/login/Login";
import { Routes, Route } from "react-router-dom";
import { BrowserRouter } from "react-router-dom";
import AllComplaints from "./pages/AllComplaints/AllComplaints";
import TodayComplaints from "./pages/AllComplaints/TodayComplaints";



function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Routes>
          <Route path="/">
            <Route index element={<Login />} />
            <Route path="home" element={<Home />} />
            <Route path="users" element={<AllComplaints />} />
            <Route path="products" element={<TodayComplaints />} />
            <Route path="login" element={<Login />} />
          </Route>
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
